body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  max-width: 1920px;
  min-width: 920px;
  width: 90%;
  text-align: center;
  margin: 0 auto;
  font-size: 62.5%;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.comment-text {
    text-align: left;
    padding: 2px;
    margin-left: 4px;
}

.comment {
    font-weight: 300;
    font-size: 1rem;
}

.user {
    font-weight: 500;
    font-size: 12px;
}


.post-container {
    height: 500px;
    width: 50%;
    margin: 5px auto;
}

.post-border {
    border: 1px solid #D3D3D3;
    margin: 5px 0;
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0, 2);
}

.post-image-wrapper {
    height: 100%;
    width: 100%;
}

.post-image {
    width: 45%;
}

.post-header {
    font-size: 1rem;
    border-radius: 50%;
    display: flex;
    margin: 9px;
}

.post-thumb-wrapper {
    height: 30px;
    width: 30px;
}

.post-thumb {
    width: 7em;
    border-radius: 50%;
}

.like-section {
    width: 100%;
    display: flex;
}

.like-section-wrapper {
    font-size: 0.7rem;
    height: 10px;
    padding: 10px;
}
/* Search Bar Syling */

.search-bar-wrap {
    border-bottom: 1px solid #3333;
    height: 80px;
    line-height: 70px;
    display: flex;
    justify-content: space-between;
    background: #ffffff;
    padding: 20px;
}

.image {
    width: 200px;
    height: 50px;
}

.logo {
    width: 200px;
    height: auto;
}

.social-wrap {
    display: flex;
}

.social {
    width:  40px;
    height: 40px;
    padding: 40px 45px 40px 3px;
}

.fa-instagram {
    position: relative;
    bottom: 90px;
    right: 180px;
    left: -140px;
    padding-right: 20px;
    border-right: 1px solid black;
}
.login-form {
    width: 500px;
    background: #D3D3D3;
    border-radius: 6px;
    margin: 10px auto;
    padding: 10px 25px;
    color: #333333;
}

button {
    background-color: #87CEEB;
    border: none;
    color: #ffffff;
    padding: 0.30em 1.2em;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 1em;
    margin: 1em;
    cursor: pointer;
    border-radius: 3px;
}

button:hover {
    background-color: #333333;
}
